import React, { useEffect, useState } from 'react';
import { database, ref, get } from '../firebase';
import { useNavigate } from 'react-router-dom';
import './src/MainContent.css';
import './src/Home.css';

export default function Home({ hideSidebar }) {
  const [posts, setPosts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const navigate = useNavigate();

  useEffect(() => {
    const fetchPosts = async () => {
      const postsRef = ref(database, 'projects'); // Adjust the path as per your Firebase structure
      try {
        const snapshot = await get(postsRef);
        if (snapshot.exists()) {
          const data = snapshot.val();
          // Convert Firebase object to an array for easier mapping
          const postsArray = Object.keys(data).map(key => ({
            id: key,
            ...data[key]
          }));
          setPosts(postsArray);
        } else {
          console.log("No posts available");
          setPosts([]); // Ensure posts are set to an empty array
        }
      } catch (error) {
        console.error('Error fetching posts:', error);
        setError('Failed to load posts.'); // Set error state
      } finally {
        setLoading(false);
      }
    };

    fetchPosts();
  }, []);

  const truncateContent = (content, wordCount) => {
    if (!content) return '';

    // Define words or patterns to remove (case-insensitive)
    const patternToRemove = /^(\[para:)|(\s*\[para:)/i;

    // Remove the specified pattern from the start of the content
    let cleanedContent = content.replace(patternToRemove, '').trim();

    // Split the cleaned content into words
    let words = cleanedContent.split(/\s+/);

    // Return truncated content
    return words.slice(0, wordCount).join(' ') + (words.length > wordCount ? '...' : '');
  };

  const formatDate = (timestamp) => {
    if (!timestamp) return 'Unknown Date'; // Fallback for undefined timestamps
    const date = new Date(timestamp);
    const day = String(date.getDate()).padStart(2, '0'); // Pad single digits
    const month = String(date.getMonth() + 1).padStart(2, '0'); // Months are zero-indexed, so add 1
    const year = date.getFullYear();
    return `${day}:${month}:${year}`;
  };
  const handlePostClick = (postId) => {
    navigate(`/main-content?postId=${postId}`); // Use navigate to go to the desired route

    // Scroll to the top of the page
    window.scrollTo(0, 0);
  };

  if (loading) {
    return <div>Loading...</div>; // Loading indicator
  }

  if (error) {
    return <div>{error}</div>; // Error message
  }

  return (
    <div id="main-content__wrapper" className={`w-full ${hideSidebar ? '' : 'md:w-2/4 main-content-wrapper'} md:pr-30px relative`}>
      <section className="section recent-post" id="recent" aria-labelledby="recent-label">
        <div className="container-home">
          <div className="post-main">
            <h2 style={{ textAlign: 'center' }} className="headline headline-2 section-title-home">
              <span className="span">Top posts</span>
            </h2>
            <p className="section-text">
              Don't miss the latest trends
            </p>
            <ul className="grid-list-home">
              {posts.map((post) => (
                <li>
                  <div className="recent-post-card">
                    <figure className="card-banner img-holder" style={{ '--width': '300', '--height': '150' }}>
                      <img src={post.headerImage} width="271" height="258"
                        alt={post.title} className="img-cover" />
                    </figure>
                    <div className="card-content">
                      <a className="card-badge">{post.category}</a>
                      <h3 style={{cursor:'pointer'}} onClick={() => handlePostClick(post.id)} className="headline headline-3 card-title">
                        <a className="link hover-2">{post.title}</a>
                      </h3>
                      <p className="card-text">
                        {truncateContent(post.content, 40)}
                      </p>
                      <div className="card-wrapper">
                        <div className="card-tag">
                          {Array.isArray(post.tags) && post.tags.length > 0 ? (
                            post.tags.map((tag, index) => (
                              <a key={index} href={`#${tag}`} className="span hover-2">#{tag}</a>
                            ))
                          ) : (
                            <span className="span no-tags">No tags available</span> // Optional fallback message
                          )}
                        </div>

                        <div className="wrapper">
                          <ion-icon name="time-outline" aria-hidden="true"></ion-icon>
                          <span className="span">{formatDate(post.created_at) || 'Unknown Date'}</span>
                        </div>
                      </div>
                    </div>
                  </div>
                </li>
              ))}
            </ul>

            <nav style={{ display: 'none' }} aria-label="pagination" className="pagination">

              <a href="#" className="pagination-btn" aria-label="previous page">
                <ion-icon name="arrow-back" aria-hidden="true"></ion-icon>
              </a>

              <a href="#" className="pagination-btn">1</a>
              <a href="#" className="pagination-btn">2</a>
              <a href="#" className="pagination-btn">3</a>
              <a href="#" className="pagination-btn" aria-label="more page">...</a>

              <a href="#" className="pagination-btn" aria-label="next page">
                <ion-icon name="arrow-forward" aria-hidden="true"></ion-icon>
              </a>

            </nav>

          </div>

        </div>
      </section>

      <section className="tags" aria-labelledby="tag-label">
        <div className="container">

          <h2 className="headline headline-2 section-title-home" id="tag-label">
            <span className="span">Popular Tags</span>
          </h2>

          <p className="section-text">
            Most searched keywords
          </p>

          <ul className="grid-list">

            <li>
              <button className="card tag-btn">
                <img src="https://picsum.photos/32/32" width="32" height="32" loading="lazy" alt="Travel" />

                <p className="btn-text">Travel</p>
              </button>
            </li>

            <li>
              <button className="card tag-btn">
                <img src="https://picsum.photos/32/32" width="32" height="32" loading="lazy" alt="Culture" />

                <p className="btn-text">Culture</p>
              </button>
            </li>

            <li>
              <button className="card tag-btn">
                <img src="https://picsum.photos/32/32" width="32" height="32" loading="lazy" alt="Lifestyle" />

                <p className="btn-text">Lifestyle</p>
              </button>
            </li>

            <li>
              <button className="card tag-btn">
                <img src="https://picsum.photos/32/32" width="32" height="32" loading="lazy" alt="Fashion" />

                <p className="btn-text">Fashion</p>
              </button>
            </li>

            <li>
              <button className="card tag-btn">
                <img src="https://picsum.photos/32/32" width="32" height="32" loading="lazy" alt="Food" />

                <p className="btn-text">Food</p>
              </button>
            </li>

            <li>
              <button className="card tag-btn">
                <img src="https://picsum.photos/32/32" width="32" height="32" loading="lazy" alt="Space" />

                <p className="btn-text">Space</p>
              </button>
            </li>

            <li>
              <button className="card tag-btn">
                <img src="https://picsum.photos/32/32" width="32" height="32" loading="lazy" alt="Animal" />

                <p className="btn-text">Animal</p>
              </button>
            </li>

            <li>
              <button className="card tag-btn">
                <img src="https://picsum.photos/32/32" width="32" height="32" loading="lazy" alt="Minimal" />

                <p className="btn-text">Minimal</p>
              </button>
            </li>

            <li>
              <button className="card tag-btn">
                <img src="https://picsum.photos/32/32" width="32" height="32" loading="lazy" alt="Interior" />

                <p className="btn-text">Interior</p>
              </button>
            </li>

            <li>
              <button className="card tag-btn">
                <img src="https://picsum.photos/32/32" width="32" height="32" loading="lazy" alt="Plant" />

                <p className="btn-text">Plant</p>
              </button>
            </li>

            <li>
              <button className="card tag-btn">
                <img src="https://picsum.photos/32/32" width="32" height="32" loading="lazy" alt="Nature" />

                <p className="btn-text">Nature</p>
              </button>
            </li>

            <li>
              <button className="card tag-btn">
                <img src="https://picsum.photos/32/32" width="32" height="32" loading="lazy" alt="Business" />

                <p className="btn-text">Business</p>
              </button>
            </li>
          </ul>
        </div>
      </section>
    </div>
  )
}
