import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom'; // Import useNavigate for redirection
import { auth } from '../firebase'; // Import Firebase authentication
import { signInWithEmailAndPassword, createUserWithEmailAndPassword } from 'firebase/auth';
import Cookies from 'js-cookie'; // For storing cookies 
import './src/Header.css';

export default function Header() {
    const [menuVisible, setMenuVisible] = useState(false);
    const [searchVisible, setSearchVisible] = useState(false);
    const [loginVisible, setLoginVisible] = useState(false);
    const [signupVisible, setSignupVisible] = useState(false);
    const [isDarkMode, setIsDarkMode] = useState(true);
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [signupEmail, setSignupEmail] = useState('');
    const [signupPassword, setSignupPassword] = useState('');
    const [error, setError] = useState('');
    const navigate = useNavigate(); // Hook for navigation

    const toggleDarkMode = () => {
        setIsDarkMode(!isDarkMode);
        document.body.classList.toggle('dark-mode', !isDarkMode);
    };

    const handleLogin = async (e) => {
        e.preventDefault();
        console.log('Login Email:', email);
        console.log('Login Password:', password);
        try {
            const userCredential = await signInWithEmailAndPassword(auth, email, password);
            // Login successful, store UID in a cookie
            const user = userCredential.user;
            console.log('User logged in:', user);
            Cookies.set('uid', user.uid, { expires: 365 }); // Set cookie with expiration
            setError('');
            setLoginVisible(false); // Close login modal
            navigate('/dashboard'); // Redirect to dashboard
        } catch (error) {
            const errorCode = error.code;
            const errorMessage = error.message;
            console.log(errorCode, errorMessage);
            setError('Invalid email or password.');
        }
    };

    const handleSignup = async (e) => {
        e.preventDefault();
        console.log('Signup Email:', signupEmail);
        console.log('Signup Password:', signupPassword);
        try {
            const userCredential = await createUserWithEmailAndPassword(auth, signupEmail, signupPassword);
            // Signed up successfully
            const user = userCredential.user;
            console.log('User signed up:', user);
            // Store UID in a cookie
            Cookies.set('uid', user.uid, { expires: 365 }); // Set cookie with expiration
            setError('');
            setSignupVisible(false); // Close sign-up modal
            navigate('/dashboard'); // Redirect to dashboard
        } catch (error) {
            const errorCode = error.code;
            const errorMessage = error.message;
            console.log(errorCode, errorMessage);
            setError(`Sign-up failed. ${errorMessage === 'auth/email-already-in-use' ? 'Email already in use.' : 'Please try again.'}`);
        }
    };

    // Check for UID and handle click event
    const handleLoginButtonClick = () => {
        const uid = Cookies.get('uid');
        if (uid) {
            navigate('/dashboard'); // Redirect to dashboard if UID exists
        } else {
            setLoginVisible(true); // Show login form if UID does not exist
            setSignupVisible(false); // Hide signup form
        }
    };

    const languages = [
        'Python', 'Java', 'Rust', 'Go', 'Kotlin Dev', 'React JS', 'Node JS', 'Angular JS',
        'Swift', 'C', 'C++', 'Android Dev', 'TypeScript', 'Ruby', 'PHP', 'Perl', 'JavaScript',
        'Scala', 'Elixir', 'Haskell', 'Dart', 'Flutter', 'Objective-C', 'MATLAB', 'R', 'VBA',
        'F#', 'Clojure', 'Shell Script', 'Lua', 'Julia', 'Erlang', 'Solidity', 'Golang'
    ];

    return (
        <>
            <header className="header" id="header">
                <nav className="nav container">
                    <Link to="#" className="nav__logo">NTXM</Link>
                    <div className={`nav__menu ${menuVisible ? 'show-menu' : ''}`} id="nav-menu">
                        <ul className="nav__list">
                            <li className="nav__item"><Link to="/" className="nav__link">Home</Link></li>
                            <li className="nav__item"><Link to="/courses" className="nav__link">Courses</Link></li>
                            <li className="nav__item"><Link to="/featured" className="nav__link">Featured</Link></li>
                            <li className="nav__item"><Link to="/contact" className="nav__link">Contact Me</Link></li>
                        </ul>
                        <div className="nav__close" id="nav-close" onClick={() => setMenuVisible(false)}>
                            <i className="ri-close-line"></i>
                        </div>
                    </div>

                    <div className="nav__actions">
                        <i className={`ri-search-line nav__search ${searchVisible ? 'show-search' : ''}`} id="search-btn" onClick={() => setSearchVisible(true)}></i>
                        <i className={`ri-user-line nav__login ${loginVisible ? 'show-login' : ''}`} id="login-btn" onClick={handleLoginButtonClick}></i>
                        <button className="dark-mode-toggle" onClick={toggleDarkMode}>
                            {isDarkMode ? 'Light Mode' : 'Dark Mode'}
                        </button>
                        <div className="nav__toggle" id="nav-toggle" onClick={() => setMenuVisible(true)}>
                            <i className="ri-menu-line"></i>
                        </div>
                    </div>
                </nav>

                <div className='header-bottom'>
                    <ul>
                        {[...languages, ...languages].map((language, index) => (
                            <li key={index}>{language}</li>
                        ))}
                    </ul>
                </div>
            </header>
            {/* Search Overlay */}
            <div className={`search ${searchVisible ? 'show-search' : ''}`} id="search">
                <form action="" className="search__form">
                    <i className="ri-search-line search__icon"></i>
                    <input type="search" placeholder="What are you looking for?" className="search__input" />
                </form>
                <i className="ri-close-line search__close" id="search-close" onClick={() => setSearchVisible(false)}></i>
            </div>
            <div className={`login ${loginVisible ? 'show-login' : ''}`} id="login">
                <form action="" className="login__form" onSubmit={handleLogin}>
                    <h2 className="login__title">Log In</h2>
                    {error && <p className="error-message">{error}</p>}
                    <div className="login__group">
                        <div>
                            <label htmlFor="email" className="login__label">Email</label>
                            <input type="email" placeholder="Write your email" id="email" className="login__input" value={email} onChange={(e) => setEmail(e.target.value)} />
                        </div>
                        <div>
                            <label htmlFor="password" className="login__label">Password</label>
                            <input type="password" placeholder="Enter your password" id="password" className="login__input" value={password} onChange={(e) => setPassword(e.target.value)} />
                        </div>
                    </div>
                    <div>
                        <p className="login__signup">
                            You do not have an account? <Link to="#" onClick={() => { setLoginVisible(false); setSignupVisible(true); }}>Sign up</Link>
                        </p>
                        <Link to="#" className="login__forgot">You forgot your password</Link>
                        <button type="submit" className="login__button">Log In</button>
                    </div>
                </form>
                <i className="ri-close-line login__close" id="login-close" onClick={() => setLoginVisible(false)}></i>
            </div>

            <div className={`signup ${signupVisible ? 'show-signup' : ''}`} id="signup">
                <form action="" className="signup__form" onSubmit={handleSignup}>
                    <h2 className="signup__title">Sign Up</h2>
                    {error && <p className="error-message">{error}</p>}
                    <div className="signup__group">
                        <div>
                            <label htmlFor="signup-email" className="signup__label">Email</label>
                            <input type="email" placeholder="Write your email" id="signup-email" className="signup__input" value={signupEmail} onChange={(e) => setSignupEmail(e.target.value)} />
                        </div>
                        <div>
                            <label htmlFor="signup-password" className="signup__label">Password</label>
                            <input type="password" placeholder="Enter your password" id="signup-password" className="signup__input" value={signupPassword} onChange={(e) => setSignupPassword(e.target.value)} />
                        </div>
                    </div>
                    <div>
                        <p className="signup__login">
                            Already have an account? <Link to="#" onClick={() => { setSignupVisible(false); setLoginVisible(true); }}>Log In</Link>
                        </p>
                        <button type="submit" className="signup__button">Sign Up</button>
                    </div>
                </form>
                <i className="ri-close-line signup__close" id="signup-close" onClick={() => setSignupVisible(false)}></i>
            </div>

        </>
    );
}
