import React, { useEffect, useState } from 'react';
import { useParams, useLocation } from 'react-router-dom';
import { database, ref, update, get } from '../firebase'; // Correct imports
import './src/MainContent.css'; // Adjust path as needed
import Cookies from 'js-cookie'; // For reading cookies

export default function MainContent() {
    const [post, setPost] = useState(null);
    const [loading, setLoading] = useState(true);
    const { postId } = useParams(); // Get postId from URL params
    const location = useLocation();

    useEffect(() => {
        let isMounted = true; // Flag to prevent double execution

        const query = new URLSearchParams(location.search);
        const id = query.get('postId') || postId;

        if (id) {
            // Function to handle Firebase updates
            const handleFirebaseUpdate = async (postId) => {
                const uid = Cookies.get('uid');
                if (!uid) {
                    console.log('No UID found in cookies');
                    return;
                }

                const userRef = ref(database, `/users/${uid}/visited-posts/${postId}`);
                const postRef = ref(database, `/projects/${postId}`);

                try {
                    const postSnapshot = await get(postRef);
                    if (postSnapshot.exists() && isMounted) {
                        const timestamp = Date.now();
                        await update(userRef, { timestamp });
                        console.log('Updated post timestamp');
                    } else if (isMounted) {
                        console.log('Post does not exist');
                    }
                } catch (error) {
                    if (isMounted) console.error('Error updating Firebase:', error);
                }
            };

            handleFirebaseUpdate(id);

            const postRef = ref(database, `/projects/${id}`);
            get(postRef).then((snapshot) => {
                if (isMounted) {
                    if (snapshot.exists()) {
                        setPost(snapshot.val());
                    } else {
                        setPost(null);
                    }
                    setLoading(false);
                }
            }).catch((error) => {
                if (isMounted) {
                    console.error("Error fetching post:", error);
                    setLoading(false);
                }
            });
        } else {
            setLoading(false);
        }

        return () => {
            isMounted = false; // Cleanup function to prevent updates on unmounted component
        };
    }, [postId, location.search]);

    const decodeHtmlEntities = (text) => {
        const textarea = document.createElement('textarea');
        textarea.innerHTML = text;
        return textarea.value;
    };

    const handleCopyCode = (code) => {
        // Replace @/space/@ with newline characters
        let formattedCode = code.replace(/@\/space\/@/g, '\n');

        // Decode HTML entities
        formattedCode = decodeHtmlEntities(formattedCode);

        // Check if the Clipboard API is supported
        if (navigator.clipboard) {
            navigator.clipboard.writeText(formattedCode)
                .then(() => {
                    alert('Code copied to clipboard!');
                })
                .catch(err => {
                    console.error('Failed to copy code: ', err);
                    alert('Failed to copy code. Please try again.');
                });
        } else {
            // Fallback for browsers that do not support Clipboard API
            const textArea = document.createElement('textarea');
            textArea.value = formattedCode;
            document.body.appendChild(textArea);
            textArea.select();
            try {
                document.execCommand('copy');
                alert('Code copied to clipboard!');
            } catch (err) {
                console.error('Failed to copy code using fallback method: ', err);
                alert('Failed to copy code. Please try again.');
            }
            document.body.removeChild(textArea);
        }
    };




    const renderContent = (content) => {
        // Split content by '],[' to handle different content blocks separately
        const contentBlocks = content.split(/,\s*(?=\[|\])/).map(block => block.trim());

        return contentBlocks.map((item, index) => {
            const cleanItem = item.startsWith('[') && item.endsWith(']')
                ? item.slice(1, -1)
                : item;

            if (cleanItem.startsWith('heading:')) {
                return <h4 key={index}>{cleanItem.replace('heading:', '').trim()}</h4>;
            }
            if (cleanItem.startsWith('boldText:')) {
                return <p style={{ marginTop: '20px' }}><strong key={index}>{cleanItem.replace('boldText:', '').trim()}</strong></p>;
            }
            if (cleanItem.startsWith('image:')) {
                return (
                    <div key={index} className="featured-img mb-3">
                        <figure className="w-full">
                            <div className="featured-img-container">
                                <img
                                    src={cleanItem.replace('image:', '').trim()}
                                    alt="Featured"
                                    className="w-full"
                                />
                            </div>
                        </figure>
                    </div>
                );
            }
            if (cleanItem.startsWith('code.')) {
                // Extract language and code from content
                const codeContent = cleanItem.replace('code.', '').trim();
                //console.log("Raw codeContent:", codeContent); // Debugging line

                // Extract language and code
                const [language, code] = codeContent.split(/@edoc@/);
                const extractedLanguage = language.replace(':', '').trim();
                const cleanCode = code ? code.replace(/@edoc@$/, '').trim() : '';
                //console.log("Extracted language:", extractedLanguage); // Debugging line
                //console.log("Clean code:", cleanCode); // Debugging line

                // Replace @/space/@ with \n for new lines
                const htmlCode = cleanCode.replace(/@\/space\/@/g, '\n');

                return (
                    <div key={index} className="chart" id={`chart${index}`}>
                        <div className="lang-cont">
                            <p>{extractedLanguage}</p>
                            <svg
                                xmlns="http://www.w3.org/2000/svg"
                                height="24px"
                                viewBox="0 -960 960 960"
                                width="24px"
                                fill="#FFFFFF"
                                onClick={() => handleCopyCode(cleanCode)}
                            >
                                <path
                                    d="M360-240q-33 0-56.5-23.5T280-320v-480q0-33 23.5-56.5T360-880h360q33 0 56.5 23.5T800-800v480q0 33-23.5 56.5T720-240H360Zm0-80h360v-480H360v480ZM200-80q-33 0-56.5-23.5T120-160v-560h80v560h440v80H200Zm160-240v-480 480Z"
                                />
                            </svg>
                        </div>
                        <div className='pro-cont'>
                            <pre>
                                <code className={`language-${extractedLanguage.toLowerCase()}`} id={`code${index}`} dangerouslySetInnerHTML={{ __html: htmlCode }} />
                            </pre>
                        </div>
                    </div>
                );
            }

            if (cleanItem.startsWith('para:')) {
                return <p key={index}>{cleanItem.replace('para:', '').trim()}</p>;
            }
            if (cleanItem.startsWith('listItem:')) {
                return <li key={index}>{cleanItem.replace('listItem:', '').trim()}</li>;
            }
            return <p key={index}>{cleanItem}</p>; // Default case
        });
    };

    if (loading) {
        return <div id="main-content__wrapper" className="w-full md:w-3/4 md:pr-30px main-content-wrapper relative">Loading...</div>;
    }

    if (!post) {
        return <div id="main-content__wrapper" className="w-full md:w-3/4 md:pr-30px main-content-wrapper relative">Post not found.</div>;
    }

    return (
        <div id="main-content__wrapper" style={{ marginTop: '20px' }} className="w-full md:w-3/4 md:pr-30px main-content-wrapper relative">
            <div className="row ">
                <div className="w-full mt-5">
                    <div className="content-wrapper post-title-wrapper relative">
                        <h1 className="farticle-title text-22px md:text-36px leading-33px md:leading-44px font-merriweather font-bold md:my-4 text-gray-500 break-words">
                            {post.title || "Post Title"}
                        </h1>
                    </div>
                </div>
            </div>
            <div className="content-wrapper article__content wysiwyg-content my-3 link__tracking">
                {post.headerImage && (
                    <div className="featured-img mb-3">
                        <figure className="w-full">
                            <div className="featured-img-container">
                                <img
                                    src={post.headerImage}
                                    alt="Featured Image"
                                    className="w-full"
                                />
                            </div>
                            <figcaption className="capitalize">{post.imageCaption || ""}</figcaption>
                        </figure>
                    </div>
                )}
                <div id="article-content__marker" className="article__content">
                    <section>
                        <div className="container">
                            {renderContent(post.content || "")}
                        </div>
                    </section>
                </div>
            </div>
            <div className="article-sharing">
                <a href="#" className="article-sharing__link article-sharing__link--facebook" target="_blank" rel="noopener noreferrer"></a>
                <a href="#" className="article-sharing__link article-sharing__link--twitter" target="_blank" rel="noopener noreferrer"></a>
                <a href="#" className="article-sharing__link article-sharing__link--linkedin" target="_blank" rel="noopener noreferrer"></a>
            </div>
        </div>
    );
}
