// src/App.js
import React, { useEffect, useState } from 'react';
import './App.css';
import RightBar from './components/RightBar';
import Footer from './components/Footer';
import Header from './components/Header';
import Home from './components/Home';
import MainContent from './components/MainContent';
import Cources from './components/Cources';
import Dashboard from './components/Dashboard'; // Import Dashboard component
import { BrowserRouter as Router, Route, Routes, useLocation } from 'react-router-dom';
import { database, ref, onValue } from './firebase';
import { ThemeProvider, useTheme } from './contexts/ThemeContext';

function AppContent() {
  const location = useLocation(); // Get the current location

  // Function to determine if the Sidebar should be visible
  const showSidebar = location.pathname !== '/' && location.pathname !== '/dashboard';

  // Determine whether to hide the sidebar in Home and Dashboard
  const hideSidebarForHomeAndDashboard = !showSidebar;

  return (
    <div className="main-container px-5 xl:px-0 article-link-wrapper">
      <div className="row right-rail-present">
        <Routes>
          <Route
            path="/"
            element={<Home hideSidebar={hideSidebarForHomeAndDashboard} />} // Pass prop to Home
          />
          <Route
            path="/main-content"
            element={<MainContent />}
          />
          <Route
            path="/courses"
            element={<Cources />}
          />
          <Route
            path="/main-content/:postId"
            element={<MainContent />}
          />
          <Route
            path="/dashboard"
            element={<Dashboard hideSidebar={hideSidebarForHomeAndDashboard} />} // Pass prop to Dashboard
          />
        </Routes>
        {showSidebar && <RightBar />} {/* Conditionally render Sidebar */}
      </div>
    </div>
  );
}



function App() {
  const [status, setStatus] = useState('Checking Firebase connection...');

  useEffect(() => {
    // Reference to the 'projects' node
    const projectsRef = ref(database, 'projects');

    // Check connection and listen for changes
    const handleData = (snapshot) => {
      if (snapshot.exists()) {
        setStatus('Firebase connected successfully.');
      } else {
        setStatus('No data available.');
      }
    };

    // Fetch data and set status
    const unsubscribe = onValue(projectsRef, handleData, {
      onlyOnce: true // Fetch data only once
    });

    // Cleanup function to remove the listener if needed
    return () => {
      if (unsubscribe) {
        unsubscribe();
      }
    };
  }, []);

  const ThemeToggleButton = () => {
    const { toggleTheme } = useTheme();
    return (
      <button onClick={toggleTheme} className="theme-toggle-button">
        Toggle Theme
      </button>
    );
  };

  return (
    <ThemeProvider>
      <Router>
        <Header />
        <ThemeToggleButton />
        <div style={{ marginTop: '30px' }} id="header-root-vue">
          <div id="root-app" className="flex flex-col min-h-screen">
            <div style={{ padding: '10px' }} id="default-page-vue-root" className="break-word">
              <AppContent /> {/* Render AppContent inside Router */}
            </div>
          </div>
        </div>
        <Footer />
        <p className='firebase-check'>{status}</p>
      </Router>
    </ThemeProvider>
  );
}

export default App;
