export default function CoursesSection() {
  const sectionRef = useRef(null); // Ref for the section

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            entry.target.classList.add('animate-in');
          }
        });
      },
      { threshold: 0.1 }
    );

    const cards = sectionRef.current.querySelectorAll('.course-card'); // Refined query
    cards.forEach((card) => observer.observe(card));

    return () => observer.disconnect();
  }, []);

  return (
    <div id="main-content__wrapper" className="w-full md:w-3/4 md:pr-30px main-content-wrapper relative">
      <section className="courses-section" ref={sectionRef}>
        <div className="container">
          <h2 className="section-title-cources">Our Courses</h2>
          <div className="courses-grid">
            {courses.map((course) => (
              <a key={course.id} href={course.link} className="course-card" target="_blank" rel="noopener noreferrer">
                <div className="course-image-container">
                  <img src={course.image} alt={`${course.title} course`} className="course-image" />
                </div>
                <h3 className="course-title">{course.title}</h3>
                <p className="course-description">{course.description}</p>
                <div className="course-details">
                  <div className="detail">
                    <i className="icon-clock"></i>
                    {course.time}
                  </div>
                  <div className="detail">
                    <i className="icon-calendar"></i>
                    {course.duration}
                  </div>
                  <div className="detail">
                    <i className="icon-book"></i>
                    {course.type}
                  </div>
                  <div className="detail">
                    <i className="icon-code"></i>
                    {course.language}
                  </div>
                </div>
                <div className="progress-bar-cources"></div>
              </a>
            ))}
          </div>
        </div>
      </section>
    </div>
  );
}
